<template>
  <section>
    <md-card>
      <md-card-content>
        <md-layout :md-gutter="true">
          <md-layout>
            <md-input-container>
              <label>Data Inicial</label>
              <md-input v-model="dataInicial" type="date" />
            </md-input-container>
          </md-layout>
          <md-layout>
            <md-input-container>
              <label>Data Final</label>
              <md-input v-model="dataFinal" type="date" />
            </md-input-container>
          </md-layout>
        </md-layout>
      </md-card-content>
      <md-card-actions>
        <md-button class="md-raised md-primary" @click="run()">Gerar</md-button>
      </md-card-actions>
    </md-card>
  </section>
</template>

<script>
import { baseURL } from "@service/config"
export default {
  data() {
    return {
      dataInicial: "",
      dataFinal: ""
    }
  },
  methods: {
    run() {
      const query = [
        "dataInicial=",
        this.dataInicial,
        "&dataFinal=",
        this.dataFinal
      ].join("")
      const url = `${baseURL}/report?${query}`

      window.open(url)
    }
  }
}
</script>

<style lang="scss" scoped>
section {
  min-width: 700px;
}
</style>



